import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Footer from "./footer"
import { Helmet } from "react-helmet"

export default function Layout({ children }) {
  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <div>
      <div className="application">
        <Helmet>
          <meta charSet="utf-8" />
          <meta property="og:type" content="website" />
          <meta property="og:title" content="Nomnom's Blog" />
          <meta
            property="og:description"
            content="A collection of useful information and tutorials. The content typically ranges between mathematics, to general information about game engines."
          />
          <meta property="og:url" content="https://nomnomab.github.io/blog/" />
        </Helmet>
      </div>

      <header className="header">
        <a className="site-icon" href="/blog" />
      </header>

      <div className="content">
        {children} <Footer />
      </div>
    </div>
  )
}
